import React from 'react';

export class KnoweledgeSection extends React.Component {
    render = () => (
            <div className="knoweledge-container">
                <p>Possuo experincia principalmente com React e tambm AngularJs.</p>

                <p>Domino HTML5, CSS 3.0, Node.js, Firebase e Git . Para javascript uso o padro es6. Tenho experincia em C#, VBA e SQL alm de Java, php, python. Como hobby eu tambm domino SketchUp e tenho familiaridade com Blender.</p>

                <p>Conheo e uso Kanban, Agile e Scrum.</p>

                <p>Tenho muita facilidade em aprender a usar novas ferramentas em pouco tempo.</p>
            </div>

    )
}


